export default {
	data() {
		return {
			userInfo:{}
		}
	},
	methods: {
		tochangeMobile(){
			// 账号管理
			uni.navigateTo({
				url:'../account-manager/account-manager'
			})
		},
		goBack(){
			uni.navigateBack({
				delta:1
			})
		},
		getUserInfo(){
			uni.getStorage({
				key:'userInfo',
				success: (res) => {
					this.userInfo=res.data
					console.log(this.userInfo)
				}
			})
		},
		
		//退出登录
		quitEvent(){
			uni.removeStorage({
				key:'userInfo',
				success: (res) => {
					console.log(res)
					uni.navigateTo({
						url:'../../../login-design/login/login'
					})
				}
			})
			
		}
	},
	onLoad(){
		this.getUserInfo()
		
	}
}
